import {
  resolve
} from "path";

const http = {
  loginleftmenu(vm) {
    vm.$axios
      .post(vm.HOST + "selectUserPage.action")
      // .get("static/json/menu.json")
      .then(function (response) {
        var res = response.data;
        if (res.code == "0") {
          var menus = vm.$api.opreate.addShow(res.userPageList);
          vm.$store.commit("UPDATE_USERPAGE", menus);
          vm.$store.commit("UPDATE_USERPERMS", res.perms);
        }
      })
      .catch(function (error) {
        console.log(error);
      });
  },
  loginTableData(vm, action, states) {
    vm.$store.commit("REMOVECHECK");
    var pages = vm.$store.getters.GETUSERTABLEPAGE;
    var data = {
      page: {
        active: pages.active,
        pagelist: pages.pagelist
      },
      state: states
    }
    var m = layer.msg("数据加载中", {
      icon: 16,
      shade: [0.1, '#ccc'] //0.1透明度的白色背景
    });
    vm.$axios
      .post(vm.HOST + action, data)
      .then(function (success) {
        var res = success.data;
        if (res.code == "0") {
          vm.$store.commit("UPDATETITLEDATA", judgeTitle(res.data.data_title));
          vm.$store.commit("UPDATELISTTOTAL", res.data.listTotal);
          vm.$store.commit("UPDATEDATAS", res.data.lists);
          vm.$store.commit("UPDATEPAGETOTAL", Math.ceil(pages.listTotal / pages.pagelist));
          vm.$store.commit("UPDATEPAGES", pagelogin(
            pages.pageTotal,
            pages.active,
            pages.count
          ));
          vm.$store.commit("UPDATECHECKDATA", []);
        } else {
          m = layer.msg("获取失败", {
            icon: 5
          });
        }
      })
      .catch(function (error) {
        console.log(error);
        vm.$store.commit("UPDATEDATAS", []);
        m = layer.msg("网络错误，请检查网络是否畅通", {
          icon: 5
        });
      });
    layer.close(m);
  },
  opreateHttp(vm, url, data, opreatetitle, successcall) {
    var bool = {
      bool: false
    };
    var parse=vm.$validator.validateAll("required|cnlength").then(function (value) {
      if (value) {
        return vm.$axios
            .post(vm.HOST + url, data)
            .then(function (success) {
              var res = success.data;
              if (res.code == "0") {
                vm.$layer.msg(opreatetitle +"成功", {icon: 1, time: 800,end: function () {
                  http.loginTableData(vm, vm.tableSelectData.selectTableurl, vm.tableSelectData.pageloginData);
                      if (typeof successcall === "function") {
                        successcall(vm);
                      }
                    }
                  });
                bool.bool = true;
                data = {};
                return bool.bool;
              } else {
                vm.$layer.msg(res.msg, {
                  icon: 2
                });
                bool.bool = false;
                return bool.bool;
              }
            })
            .catch(function (error) {
              vm.$layer.msg(
                '网络错误，请检查网络是否畅通', {
                  icon: 5
                }
              );
              bool.bool = false;
              return bool.bool;
        })
      }
    }).catch(error => {
      console.log(error)
      bool.bool = false;
      return bool.bool;
    });
    return parse;
  },

}
export default http;



//过滤表格title
function judgeTitle(data) {
  var datas = data;
  if (datas.length) {
    for (var i = 0; i < datas.length; i++) {
      if (datas[i] == "密码") {
        datas = datas.splice(i, 1);
      }
    }
    return datas;
  } else {
    return [];
  }
}

function datafilter(value, type) {
  //为小于10的数字加0
  function addZero(val) {
    if (val < 10) {
      return "0" + val;
    } else {
      return val;
    }
  }
  var date = new Date(Number(value));
  var year = addZero(date.getFullYear());
  var month = addZero(date.getMonth() + 1);
  var day = addZero(date.getDate());
  if (type == "yyyyMMdd") {
    return year + "年" + month + "月" + day + "日";
  }
}
//过滤函数
function judgeData(data) {
  var vm = this;
  var datas = data;
  if (datas.length > 0) {
    datas.forEach(function (Data) {
      for (var key in Data) {
        switch (key) {
          case "password":
            delete Data[key];
            break;
          case "regtime":
            Data[key] = datafilter(Data[key], "yyyyMMdd");
            break;
          case "state":
            if (Number(Data[key]) == 0) {
              Data[key] = "<div style='background:#ccc'>注销</div>";
            } else if (Number(Data[key]) == 1) {
              Data[key] = "<div style='background:#00ff00'>正常</div>";
            } else if (Number(Data[key]) == 2) {
              Data[key] = "<div style='background:#ff000f'>冻结</div>";
            }
            break;
          case "role_state":
            if (Number(Data[key]) == 0) {
              Data[key] = "<div style='background:#ccc'>未审核</div>";
            } else if (Number(Data[key]) == 1) {
              Data[key] = "<div style='background:#00ff00'>已审核</div>";
            } else if (Number(Data[key]) == 2) {
              Data[key] = "<div style='background:#ff000f'>冻结</div>";
            }
            break;
          case "type":
            if (Number(Data[key]) == 0) {
              Data[key] = "<div style='background:#ccc'>目录</div>";
            } else if (Number(Data[key]) == 1) {
              Data[key] = "<div style='background:#00ff00'>菜单</div>";
            } else if (Number(Data[key]) == 2) {
              Data[key] = "<div style='background:#ff000f'>按钮</div>";
            }
            break;
        }
      }
    });
  } else {
    datas = "";
  }
  return datas;
}
//生成页数
function pagelogin(total, active, count) {
  var from = active - Math.floor(count / 2); //最左侧页码
  var to = active + Math.floor(count / 2); //最右侧页码
  if (from <= 0) {
    from = 1;
    to = from + count - 1;
  }
  if (to > total) {
    to = total;
    from = to - count + 1;
  }
  var range = [];
  for (var i = from; i <= to; i++) {
    if (i > 0) {
      range.push(i);
    }
  }
  if (range.length > 0) {
    range.push("»");
    range.unshift("«");
    range.push("尾页");
    range.unshift("首页");
  }
  var pages = range;
  return pages;
}



